  margin: 0;
  padding: 0;
  font-family: "Lexend Deca", "Arial", sans-serif;
  color: #13aa65;
}

body {
  background-color: #FFFFFF;
}

.btn--skip {
  margin: 0 auto;
  background-color: transparent;
  border: none;
  padding: 0.5rem;
  position: fixed;
  top: 60%;
  left: 48.5%;
  text-align: center;
  font-size: 2rem;
  z-index: 99;
}

.btn--skip:hover {
  cursor: pointer;
}

.splash-container--overlay {
  background-color: #FFFFFF;
  width: 100%;
  height: 100%;
  position: fixed;
  inset: 0;
  z-index: 98;
}

.splash {
  opacity: 0;
  visibility: hidden;
  transform: translateY(0px);
  background-color: #FFFFFF;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  width: 100%;
  height: 100%;
  transition: opacity 1s ease, transform 1s ease;
}
.splash p {
  font-size: 3rem;
}

.grid-wrapper {
  width: 100%;
  height: 100vh;
  display: grid;
  justify-items: center;
  grid-template-columns: 15% auto 15%;
  grid-template-rows: 0.5fr 2fr 0.25fr;
  column-gap: 1rem;
  opacity: 0;
  transform: translateY(0px);
  transition: opacity 1s ease;
}
.grid-wrapper > * {
  width: 100%;
}
.grid-wrapper .hamburger {
  grid-column: 1/2;
  grid-row: 1/2;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  gap: 5px;
  padding-top: 1rem;
  background-color: white;
  transition: transform 0.3s ease;
}
.grid-wrapper .hamburger span {
  width: 25px;
  height: 3px;
  background-color: #13aa65;
  transition: 0.3s ease;
}
.grid-wrapper .hamburger.active {
  background-color: #13aa65;
}
.grid-wrapper .hamburger.active span {
  background-color: #FFFFFF;
}
.grid-wrapper .hamburger.active span:nth-child(1) {
  transform: rotate(45deg) translate(5px, 5px);
}
.grid-wrapper .hamburger.active span:nth-child(2) {
  opacity: 0;
}
.grid-wrapper .hamburger.active span:nth-child(3) {
  transform: rotate(-45deg) translate(6px, -6px);
}
.grid-wrapper .nav-container {
  grid-column: 1/2;
  grid-row: 2/4;
}
.grid-wrapper .nav-container .nav-menu {
  padding: 1rem 0 0 1.5rem;
  display: flex;
  height: 100%;
  font-size: 2.5rem;
  gap: 2rem;
  flex-direction: column;
  list-style: none;
  transition: 0.3s ease;
  position: relative;
  top: 0;
  left: 0;
  opacity: 0;
  transition: transform 0.3s ease;
  transform: translateX(-100%);
}
.grid-wrapper .nav-container .nav-menu.active {
  opacity: 1;
  transform: translateX(0);
  background-color: #13aa65;
  transition: transform 0.3s ease;
}
.grid-wrapper .nav-container .nav-menu.closed {
  opacity: 0;
  transform: translateX(-100%);
  transition: transform 0.3s ease;
}
.grid-wrapper .nav-container .nav-menu a {
  text-decoration: none;
  color: #FFFFFF;
  transition: transform 0.3s ease;
}
.grid-wrapper .nav-container .nav-menu a:hover {
  text-decoration: underline;
}
.grid-wrapper .resources-container {
  grid-column: 3/4;
  grid-row: 1/2;
  display: flex;
  gap: 1rem;
  justify-content: space-evenly;
  align-items: center;
}
.grid-wrapper .resources-container i {
  cursor: pointer;
  font-size: 1.75rem;
}
.grid-wrapper .main-container {
  grid-column: 2/3;
  grid-row: 2/3;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  width: 100%;
}
.grid-wrapper .main-container--grid {
  display: grid;
  grid-template-rows: 2fr 1fr;
  gap: 2rem;
}
.grid-wrapper .main-container .main-container__header {
  margin: 1rem;
  display: flex;
  flex-direction: column;
  width: fit-content;
  align-self: center;
  margin-right: 40%;
}
.grid-wrapper .main-container .main-container__header h1 {
  font-size: 3rem;
  font-weight: 400;
}
.grid-wrapper .main-container .main-container__header h2 {
  font-size: 1.5rem;
  text-align: right;
  font-weight: 200;
}
.grid-wrapper .main-container .main-container__form {
  grid-column: 2/3;
  grid-row: 2/3;
  display: flex;
  height: 100%;
}
.grid-wrapper .main-container .main-container__form form {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}
.grid-wrapper .main-container .main-container__form form textarea {
  resize: none;
  border: 2px #13aa65 solid;
  border-radius: 1rem;
  width: 50%;
  height: 65%;
  min-height: 50%;
  padding: 1rem;
  transition: 0.3s;
}
.grid-wrapper .main-container .main-container__form form textarea::placeholder {
  font-style: italic;
  color: grey;
}
.grid-wrapper .main-container .main-container__form form textarea:focus {
  outline: none;
  font-style: normal;
  color: black;
  box-shadow: -5px 5px 0px 1px #13aa65;
  -webkit-box-shadow: -5px 5px 0px 1px #13aa65;
  -moz-box-shadow: -5px 5px 0px 1px #13aa65;
}
.grid-wrapper .main-container .main-container__form form textarea:focus::placeholder {
  color: transparent;
}
.grid-wrapper .main-container .chat-wrapper {
  border: 2px solid #13aa65;
  border-radius: 1rem;
  margin: 1rem;
  padding: 1rem;
  overflow-y: auto;
}
.grid-wrapper .main-container .chat__form {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100%;
}
.grid-wrapper .main-container .chat__form .chat__textarea {
  height: 100%;
  resize: none;
  border: 2px #13aa65 solid;
  border-radius: 1rem;
  width: 75%;
  padding: 1rem;
  transition: 0.3s;
}
.grid-wrapper .main-container .chat__form .chat__textarea:focus {
  outline: none;
  font-style: normal;
  color: black;
  box-shadow: -5px 5px 0px 1px #13aa65;
  -webkit-box-shadow: -5px 5px 0px 1px #13aa65;
  -moz-box-shadow: -5px 5px 0px 1px #13aa65;
}
.grid-wrapper .btn {
  font-size: 1.15rem;
  border: 2px #13aa65 solid;
  border-radius: 0.5rem;
  margin: 1rem;
  background-color: #13aa65;
  color: #FFFFFF;
  font-weight: 500;
  padding: 0.5rem 1rem;
  width: fit-content;
  transition: 0.3s;
}
.grid-wrapper .btn:hover {
  box-shadow: -3px 4px 0px 1px #70ca70;
  -webkit-box-shadow: -3px 4px 0px 1px #70ca70;
  -moz-box-shadow: -3px 4px 0px 1px #70ca70;
  cursor: pointer;
}

@keyframes fadeOutUp {
  to {
    opacity: 0;
    transform: translateY(-20px);
  }
}
@keyframes fadeInUp {
  to {
    opacity: 1;
    transform: translateY(0);
  }
